 .navlinksmobile{
    background-color:#e5e5e5;
     position: absolute;
left:0;
top:75px;
height: auto;
width:100%;

     
} 
.a1,.a2,.a3,.a4,.a5,.resume{
     display: block;
     text-align: center;
     padding: 32px;
     transition: all 0.5s ease-out;
width:100%;
}
.mobilemenuicon
{
     
  display: block;
  position: absolute;
  font-size: 30px;
  background-color: black;
  color: #e5e5e5;
  border: none;
  outline: none;
  top: 15px;
  right: 25px;
}  

}

.resume{
     border: none;
     font-weight: 550;
   padding: 5px;
   color: black;
   cursor: pointer;
   border-radius: 50%;
   min-width: 120px;
   transition: 0.4s ease;
   font-size: larger;
   }
